import styled from 'styled-components';

const Input = styled.input`
    height: 27px;
    width: 100%;
    font-size: 12px;
    border: 1px solid #ccc;
    padding: 3px 3px 3px 5px;
    margin: 0 0 11px 0;
    box-sizing: border-box;
    outline: 0;
    resize: none;
`;

export default Input;